.social-nav {
	flex-grow: 1;
	background-color: $grey;
	ul {
		display: flex;
		justify-content: space-around;
	}
	li {
		margin-right: 10px;
		svg {
			height: 20px;
			width: auto;
			path {
				fill: white;
			}
		}
	}
	a {
		color: white;
		text-decoration: none;
	}
}



